import React, { Component } from 'react';

import './item-status-filter.css';

export default class ItemStatusFilter extends Component {
  constructor() {
    super()

    this.state = {
      status: [
        {name: "all", label: "Все"},
        {name: "active", label: "Активные"},
        {name: "done", label: "Выполненные"}
      ]
    }
  }

  render() {
    const { status } = this.state;
    const { filter: isActive, onFilterChanges } = this.props;
    const button = status.map( ({name, label}) => {
      const classElement = isActive === name 
      ? "btn-info" 
      : "btn-outline-secondary";
      return (
      <button type="button"
        className= {`btn ${classElement}`}
        key = {name}
        onClick = {() => onFilterChanges(name)}>
        {label}
      </button>
      )
    })
    return (
      <div className="btn-group">
        {/* <button type="button"
                className="btn btn-info">All</button>
        <button type="button"
                className="btn btn-outline-secondary">Active</button>
        <button type="button"
                className="btn btn-outline-secondary">Done</button> */}
          { button }
      </div>
    );
  }
}