import React, { Component } from 'react';
import './app.css';

import AppHeader from '../app-header/';
import SearchPanel from '../search-panel/';
import TodoList from '../todo-list/';
import ItemStatusFilter from '../item-status-filter/';
import AddItemForm from "../add-item-form";

export default class App extends Component {
  constructor() {
    super()
    
    this.maxID = 100;

    this.createItem = (label) => {
      return {
        label: label,
        done: false,
        important: false,
        id: this.maxID++
      }
    }

    this.state = {
      todoData: [
        this.createItem('Drink Coffee'),
        this.createItem('Make Awesome App'),
        this.createItem('Have a lunch')
      ],
      visibleData: "",
      filter: "all"
    }

    this.toggleProper = (arr, id, propName) => {
      const idx = arr.findIndex( (el) => el.id === id);
      const oldItem = arr[idx];
      const newItem = { ...oldItem, [propName]: !oldItem[propName]};

      return [...arr.slice(0, idx),
        newItem,
        ...arr.slice(idx + 1)]
    }

    this.deleteItem = (id) => {
      this.setState(( { todoData } ) => {
        const idx = todoData.findIndex( (el) => el.id === id);
        const newArray = [
          ...todoData.slice(0, idx), 
          ...todoData.slice(idx + 1)
        ];
        
        return {
          todoData: newArray
        }
      })
    }
    // конец функции удаления элемента

    this.addItem = ( goal ) => {
      const newItem = this.createItem(goal);

      this.setState( ({todoData}) => {
        const newArray = [ ...todoData, newItem];

        return {
          todoData: newArray
        }
      })
    }
    // конец функции добавления элемента

    this.toggleImportant = (id) => {
      this.setState( ( { todoData } ) => {
        return {
          todoData: this.toggleProper(todoData, id, "important")
        }
      }) 
    }
    // Конец функции изменения важности

    this.toggleDone = (id) => {
      this.setState( ( { todoData }) => {
        return {
          todoData: this.toggleProper(todoData, id, "done")
        }
      } )
    }
    // Конец функции изменения готовности элемента

    this.itemsShow = (data, string) => {
      if (string.length === 0) {
        return data
      }

      return data.filter( (el) => el.label.toLowerCase()
                        .indexOf(string.toLowerCase()) > -1)
    }
    // конец функции вывода объектов

    this.onSearchItem = (string) => {
      this.setState({
        visibleData: string
      })
    }
    // Конец функции поиска
    this.onFilterItem = (string) => {
      this.setState({
        filter: string
      })
    }
    // Конец функции изменения фильтра

    this.filterItems = (data, filter) => {
      switch(filter) {
        case "all": return data;
        case "active": return data.filter( (el) => !el.done);
        case "done": return data.filter( (el) => el.done);
        default: return data;
      }
    }
  }
  // конец Конструктора
    render() {
      const {todoData, visibleData} = this.state;
      const doneCount = todoData.filter( (el) => el.done).length;
      const toDoCount = todoData.length - doneCount;
      const visibleItems = this.filterItems(this.itemsShow(todoData, visibleData), this.state.filter);
      return (
        <div className="todo-app">
        <AppHeader toDo={ toDoCount } done={ doneCount } />
        <div className="top-panel d-flex">
          <SearchPanel onSearchItem = { this.onSearchItem }/>
          <ItemStatusFilter filter = { this.state.filter }
            onFilterChanges = { this.onFilterItem }
           />
        </div>
  
        <TodoList 
        todos={visibleItems} 
        onDeleteItem = { this.deleteItem }
        onToggleImportant = { this.toggleImportant }
        onToggleDone = { this.toggleDone }
        />
        <AddItemForm addItem = { this.addItem } ></AddItemForm>
      </div>
      )

  };
};
