import React, { Component } from 'react';

import './search-panel.css';

export default class SearchPanel extends Component {
  constructor() {
    super()

    this.state = {
      labelSearch: ""
    }

    this.searchLabel = (string) => {
      this.props.onSearchItem(string);

      this.setState({
        labelSearch: string
      })
    }
  }
  render() {
    return (
      <input type="text"
              className="form-control search-input"
              placeholder="Поиск.."
              value = {this.state.labelSearch}
              onChange = {(e) => this.searchLabel(e.target.value)}
               />
    );
  }
};